  .checkbox {
    width: 40px;
    height: 20px;
    -webkit-appearance: none;
    -moz-appearance: none;
    background: #c6c6c6;
    outline: none;
    border-radius: 10px;
    box-shadow: inset 0 0 5px rgba(0,0,0, .2);
    color: grey;
    transition: 0.5s;
    position: relative;
  }
 .checkbox[type="checkbox"] {
    background: #02a9f4;
  }
  .checkbox::before {
    content: '';
    position: absolute;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    top: 0;
    left: 0;
    background: #fff;
    transform: scale(1.1);
    box-shadow: 0 2px 5px rgba(0,0,0, .2);
    transition: 0.5s;
  }
  input:checked[type="checkbox"]::before {
    left: 20px;
  }